@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
html{font-size:18px}html *{font-family:'Poppins', sans-serif}body{margin:0px}.container{box-sizing:border-box;width:100%}header,main,footer,.boots{box-sizing:border-box;padding:2rem 160px;width:100%;margin:auto}button:hover:enabled{cursor:pointer;-webkit-filter:brightness(1.5) saturate(0.4);filter:brightness(1.5) saturate(0.4)}a:hover{cursor:pointer;-webkit-filter:brightness(0.5) saturate(0.4);filter:brightness(0.5) saturate(0.4)}.logo:hover{cursor:pointer}button{color:white;background-color:#2acfcf;border:none;border-radius:50px;padding:0.4em 1.5em}header{background-image:url(/shorten-link/static/media/illustration-working.c9b5ef54.svg);background-repeat:no-repeat;background-size:34rem;background-position:110%}header nav{display:flex;justify-content:space-between;align-items:center}header nav button,header nav a{font-size:0.75rem;font-weight:500}header nav a{text-decoration:none;color:gray}header nav .left-bar,header nav .right-bar{display:flex;align-items:center;grid-gap:20px;gap:20px}header nav .logo{width:85px}header section{padding:125px 0px}header section button{font-size:0.9rem;font-weight:600}header section .intro{box-sizing:border-box;width:50%}header section .intro .tittle{margin:0px;font-weight:700;font-size:3rem;line-height:3.5rem;color:rgba(0,0,0,0.815)}header section .intro .des{font-size:0.95rem;color:gray;margin:0px 0px 20px}main{box-sizing:border-box;background-color:rgba(222,216,238,0.384);padding-bottom:80px;position:relative}main .main-info{width:60%;margin:60px auto;text-align:center}main .main-info .tittle{font-size:1.9rem;margin:0.5rem;color:rgba(0,0,0,0.815)}main .main-info .des{font-size:0.9rem;font-weight:500;margin:0px;line-height:1.65rem;color:rgba(128,128,128,0.63)}main .main-cards{display:flex;grid-gap:25px;gap:25px;padding:17px;position:relative}main .card{box-sizing:border-box;padding:50px 30px 30px;width:100%;height:100%;background-color:white;box-shadow:0px 0px 3px 1px rgba(184,184,184,0.055);border-radius:7px;position:relative}main .card .icon{background-color:#3b3054;padding:18px;border-radius:50%;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;position:absolute;top:0px;-webkit-transform:translate(0%, -50%);transform:translate(0%, -50%)}main .card .icon img{aspect-ratio:1/1;width:32px;margin:0px}main .card .card-tittle{color:rgba(0,0,0,0.815);margin:5px 0px}main .card .card-des{color:rgba(128,128,128,0.63);margin:0px;font-size:0.85rem}main .card:nth-of-type(3){margin-top:34px}main .card:nth-of-type(4){margin-top:68px}main .line{width:60%;height:0px;position:absolute;top:40%;left:50%;border:4px solid #2acfcf;-webkit-transform:translate(-50%);transform:translate(-50%)}main .link-card-holder{margin-top:5vh;grid-gap:15px;gap:15px;display:flex;flex-direction:column}main .link-card-holder .link-card{box-sizing:border-box;padding:10px 20px;width:100%;height:100%;box-shadow:0px 0px 3px 1px rgba(184,184,184,0.055);border-radius:7px;background-color:white;display:flex;justify-content:space-between;align-items:center}main .link-card-holder .shorten{display:flex;align-items:center}main .link-card-holder a{margin:0px;margin-right:20px;font-size:0.8rem;text-decoration:none;color:black}main .link-card-holder .short-link{-webkit-text-decoration:dotted;text-decoration:dotted;color:#2acfcf}main .link-card-holder .link-err{color:#cf432a}main .link-card-holder button{border-radius:5px}main .link-card-holder button:disabled{cursor:default;background-color:#728392}.input-wrapper{background-color:#3b3054;background-image:url(/shorten-link/static/media/bg-boost-desktop.772154c9.svg);background-size:cover;background-repeat:no-repeat;text-align:center;position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);top:0;left:50%;border-radius:8px;grid-gap:20px;gap:20px;padding:35px 45px;box-sizing:border-box;width:calc(100% - 160px * 2);display:flex}.input-wrapper input{flex-grow:1;width:70%;border:none;border-radius:5px;padding:10px 20px;color:#707070;font-weight:500}.input-wrapper button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:5px;font-weight:600}.input-wrapper button img{vertical-align:middle;width:50px;height:20px;object-fit:cover}.input-wrapper input:disabled{background-color:rgba(255,255,255,0.979)}.input-wrapper button:disabled{background-color:rgba(255,255,255,0.979)}.boots{background-color:#3b3054;background-image:url(/shorten-link/static/media/bg-boost-desktop.772154c9.svg);background-size:cover;background-repeat:no-repeat;text-align:center;padding:45px 0px}.boots .tittle{color:white;font-size:1.75rem;margin:5px}.boots button{font-weight:600}footer{background-color:#232127;color:white;font-size:0.9rem;display:flex;padding-top:50px;padding-bottom:50px}footer .logo-wrapper{width:35%;-webkit-filter:brightness(0) invert(1);filter:brightness(0) invert(1)}footer .logo-wrapper img{width:100px}footer .links{width:15%}footer .links h4{margin:0px 0px 5px}footer .links a{display:block;color:#b5b5b5;font-size:0.8rem;margin:10px 0px}footer .social-media{width:20%}footer .social-media img{margin:0px 10px}@media (min-width: 1800px){main .link-card-holder{margin-top:0px}header,main,footer,.boots{padding:5rem 300px}header{padding-top:2rem}.input-wrapper{width:calc(100% - 300px * 2)}}@media (max-width: 1600px){main .link-card-holder{margin-top:50px}header,main,footer,.boots{padding:2rem 160px}header{background-size:32rem}.input-wrapper{width:calc(100% - 160px * 2)}}@media (max-width: 1300px){main .link-card-holder{margin-top:50px}header,main,footer,.boots{padding:2rem 100px}header{background-size:30rem}.input-wrapper{width:calc(100% - 100px * 2)}}@media (max-width: 950px){main .link-card-holder{margin-top:70px}html{font-size:15px}header,main,footer,.boots{padding:10px 10px}header{background-size:25rem}.input-wrapper{width:calc(100% - 10px * 2)}}@media (max-width: 600px){header{background-position:50% 15%}header section{width:100%}header section .intro{margin-top:200px;width:100%;text-align:center}main{padding-top:80px}main .main-info{width:80%}main .line{height:80%;width:0;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}main .main-cards{flex-direction:column}main .main-cards .card{margin:15px 0px}main .main-cards .card .icon{left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}main .input-wrapper{flex-direction:column;padding:25px}main .input-wrapper *{padding:15px;box-sizing:border-box;width:100%}main .input-wrapper img{width:80px;height:40px}main .link-card-holder{margin-top:25px}main .link-card-holder .link-card{flex-direction:column}footer{padding:50px;flex-direction:column;align-items:center}footer .links,footer .social-media,footer .logo-wrapper{width:80%;margin:auto;text-align:center;margin:15px}.boots{padding:50px}}

